<template>
  <div class="home-view">
    <!-- 使用背景组件 -->
    <BackGround />
    <TopMenu />
  </div>
  <div class="home">
    <!-- 管理员切换按钮 -->
    <div class="admin-switch" v-if="isAdmin">
      <div class="switch-tabs">
        <button 
          :class="['tab-btn', { active: currentTab === 'account' }]"
          @click="currentTab = 'account'"
        >
          <i class="fas fa-user-cog"></i> 账号设置
        </button>
        <button 
          :class="['tab-btn', { active: currentTab === 'users' }]"
          @click="currentTab = 'users'"
        >
          <i class="fas fa-users-cog"></i> 用户管理
        </button>
        <button 
          :class="['tab-btn', { active: currentTab === 'site' }]"
          @click="currentTab = 'site'"
        >
          <i class="fas fa-cog"></i> 站点配置
        </button>
      </div>
    </div>

    <!-- 账号设置 -->
    <SetAccount v-if="currentTab === 'account'" />
    
    <!-- 用户管理 -->
    <UserManagement v-if="currentTab === 'users'" />
    
    <!-- 站点配置 -->
    <SiteSettings v-if="currentTab === 'site'" />
  </div>
  <FootMenu />
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import TopMenu from '../components/Top-Menu.vue';
import BackGround from '../components/Back-Ground.vue';
import FootMenu from '../components/Foot-Menu.vue';
import SetAccount from './child/setting/Set-Account.vue';
import UserManagement from './child/setting/User-Management.vue';
import SiteSettings from './child/setting/Site-Settings.vue';
import { getCurrentUser, ensureVisitJwt } from '@/utils/auth.js';

// 响应式数据
const currentTab = ref('account');
const currentUser = ref(null);

// 计算属性
const isAdmin = computed(() => {
  return currentUser.value && currentUser.value.is_admin === true;
});

// 生命周期
onMounted(async () => {
  try {
    const hasJwt = await ensureVisitJwt();
    
    if (hasJwt) {
      const user = getCurrentUser();
      currentUser.value = user;
    }
  } catch (error) {
    console.error('用户信息获取失败:', error);
  }
});
</script>

