<template>
  <Teleport to="body">
    <div class="modal-overlay" @click="closeDialog">
      <div class="modal-content" @click.stop>
        <div class="modal-header">
          <h3 class="modal-title">修改邮箱</h3>
          <button class="modal-close" @click="closeDialog">
            <i class="fas fa-times"></i>
          </button>
        </div>
        <div class="modal-body">
          <div class="current-info" v-if="currentEmail">
            <label class="info-label">当前邮箱</label>
            <div class="info-value">{{ currentEmail }}</div>
          </div>
          <div class="form-group">
            <label class="form-label">新邮箱</label>
            <input
              v-model="formData.newEmail"
              type="email"
              class="form-input"
              placeholder="请输入新邮箱地址"
              @blur="validateField('newEmail')"
              @input="clearError('newEmail')"
            />
            <div v-if="errors.newEmail" class="error-message">
              {{ errors.newEmail }}
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button class="modal-btn secondary" @click="closeDialog">取消</button>
          <button 
            class="modal-btn primary" 
            :disabled="!isFormValid || loading"
            @click="handleSubmit"
          >
            <span v-if="loading" class="loading-spinner"></span>
            {{ loading ? '修改中...' : '确认修改' }}
          </button>
        </div>
      </div>
    </div>
  </Teleport>
</template>

<script setup>
import { ref, reactive, computed, defineProps, defineEmits } from 'vue';
import { ElMessage } from 'element-plus';
import { request } from '@/utils/auth.js';
// 不再使用updateEmail，直接fetch请求后端

// Props
const props = defineProps({
  currentEmail: {
    type: String,
    default: ''
  }
});

// Emits
const emit = defineEmits(['close', 'success']);

// 响应式数据
const loading = ref(false);
const formData = reactive({
  newEmail: ''
});

const errors = reactive({
  newEmail: ''
});

// 验证单个字段
const validateField = (field) => {
  const value = formData[field];
  
  switch (field) {
    case 'newEmail':
      if (!value.trim()) {
        errors[field] = '请输入新邮箱地址';
      } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value)) {
        errors[field] = '请输入有效的邮箱地址';
      } else if (value === props.currentEmail) {
        errors[field] = '新邮箱不能与当前邮箱相同';
      } else {
        errors[field] = '';
      }
      break;
  }
};

// 清除错误
const clearError = (field) => {
  if (errors[field]) {
    errors[field] = '';
  }
};

// 表单是否有效
const isFormValid = computed(() => {
  const hasValues = formData.newEmail;
  const noErrors = !Object.values(errors).some(error => error !== '');
  return hasValues && noErrors;
});

// 提交表单
const handleSubmit = async () => {
  // 验证所有字段
  Object.keys(formData).forEach(field => {
    validateField(field);
  });
  if (!isFormValid.value) {
    ElMessage.error('请检查输入内容');
    return;
  }
  loading.value = true;
  try {
    const result = await request('/api/auth/account-settings-api.php', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      body: new URLSearchParams({
        action: 'update_email',
        new_email: formData.newEmail
      })
    });
    
    if (result.status === 'ok') {
      ElMessage.success(result.message || '邮箱修改成功');
      emit('success');
      closeDialog();
    } else {
      ElMessage.error(result.message || '邮箱修改失败');
    }
  } catch (error) {
    console.error('邮箱修改失败:', error);
    ElMessage.error('邮箱修改失败');
  } finally {
    loading.value = false;
  }
};

// 关闭对话框
const closeDialog = () => {
  emit('close');
};
</script>

<style scoped>
@import '../share/modal-styles.css';

/* 额外的当前信息样式 */
.current-info {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  padding: 12px;
  margin-bottom: 16px;
}

.info-label {
  display: block;
  margin-bottom: 6px;
  font-weight: 500;
  color: #e8e8e8;
  font-size: 14px;
}

.info-value {
  color: #b0b0b0;
  font-size: 14px;
  font-family: monospace;
}

.error-message {
  color: #ff6b6b;
  font-size: 12px;
  margin-top: 4px;
}
</style>
