<template>
  <Teleport to="body">
    <div class="modal-overlay" @click="closeDialog">
    <div class="modal-content" @click.stop>
      <div class="modal-header">
        <h3>修改账户名</h3>
        <button class="modal-close" @click="closeDialog">
          <i class="fas fa-times"></i>
        </button>
      </div>
      
      <div class="modal-body">
        <div class="warning-notice">
          <div class="warning-icon">
            <i class="fas fa-exclamation-triangle"></i>
          </div>
          <div class="warning-text">
            <h4>重要提醒</h4>
            <p>修改账户名后，您的登录令牌将失效，系统会自动退出登录，需要使用新账户名重新登录。</p>
          </div>
        </div>
        
        <div class="current-info" v-if="currentUsername">
          <label class="info-label">当前账户名</label>
          <div class="info-value">{{ currentUsername }}</div>
        </div>
        
        <div class="form-group">
          <label class="form-label">新账户名</label>
          <input
            v-model="formData.newUsername"
            type="text"
            class="form-input"
            placeholder="请输入新账户名"
            @blur="validateField('newUsername')"
            @input="clearError('newUsername')"
          />
          <div v-if="errors.newUsername" class="error-message">
            {{ errors.newUsername }}
          </div>
          <div class="form-hint">
            账户名用于登录，建议使用字母、数字或下划线，长度3-20位
          </div>
        </div>
      </div>
      
      <div class="modal-footer">
        <button class="modal-btn secondary" @click="closeDialog">取消</button>
        <button 
          class="modal-btn primary" 
          :disabled="!isFormValid || loading"
          @click="handleSubmit"
        >
          <i v-if="loading" class="fas fa-spinner fa-spin"></i>
          {{ loading ? '修改中...' : '确认修改' }}
        </button>
      </div>
    </div>
  </div>
  </Teleport>
</template>

<script setup>
import { ref, reactive, computed, defineProps, defineEmits } from 'vue';
import { ElMessage } from 'element-plus';
import { request } from '@/utils/auth.js';

// Props
const props = defineProps({
  currentUsername: {
    type: String,
    default: ''
  }
});

// Emits
const emit = defineEmits(['close', 'success']);

// 响应式数据
const loading = ref(false);
const formData = reactive({
  newUsername: ''
});

const errors = reactive({
  newUsername: ''
});

// 验证单个字段
const validateField = (field) => {
  const value = formData[field];
  
  switch (field) {
    case 'newUsername':
      if (!value.trim()) {
        errors[field] = '请输入新账户名';
      } else if (value.length < 3 || value.length > 20) {
        errors[field] = '账户名长度应为3-20位';
      } else if (!/^[a-zA-Z0-9_]+$/.test(value)) {
        errors[field] = '账户名只能包含字母、数字和下划线';
      } else if (value === props.currentUsername) {
        errors[field] = '新账户名不能与当前账户名相同';
      } else {
        errors[field] = '';
      }
      break;
  }
};

// 清除错误
const clearError = (field) => {
  if (errors[field]) {
    errors[field] = '';
  }
};

// 表单是否有效
const isFormValid = computed(() => {
  const hasValues = formData.newUsername.trim();
  const noErrors = !Object.values(errors).some(error => error !== '');
  return hasValues && noErrors;
});

// 提交表单
const handleSubmit = async () => {
  // 验证所有字段
  Object.keys(formData).forEach(field => {
    validateField(field);
  });
  
  if (!isFormValid.value) {
    ElMessage.error('请检查输入内容');
    return;
  }
  
  loading.value = true;
  try {
    const result = await request('/api/auth/account-settings-api.php', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      body: new URLSearchParams({
        action: 'update_username',
        new_username: formData.newUsername.trim()
      })
    });
    
    if (result.status === 'ok') {
      ElMessage.success(result.message || '账户名修改成功');
      emit('success', formData.newUsername.trim());
      
      // 修改成功后强制退出登录
      setTimeout(async () => {
        try {
          await fetch('/api/auth/logout.php', {
            method: 'POST',
            credentials: 'include'
          });
        } catch (error) {
          console.error('退出登录失败:', error);
        }
        
        // 跳转到登录页面
        window.location.href = '/login';
      }, 1500);
      
      closeDialog();
    } else {
      ElMessage.error(result.message || '账户名修改失败');
    }
  } catch (error) {
    console.error('账户名修改失败:', error);
    ElMessage.error('账户名修改失败');
  } finally {
    loading.value = false;
  }
};

// 关闭对话框
const closeDialog = () => {
  emit('close');
};
</script>

