<template>
  <Teleport to="body">
    <div class="modal-overlay" @click="closeDialog">
      <div class="modal-content" @click.stop>
        <div class="modal-header">
          <h3>修改密码</h3>
          <button class="modal-close" @click="closeDialog">
            <i class="fas fa-times"></i>
          </button>
        </div>
      
      <div class="modal-body">
        <div class="form-group">
          <label class="form-label">当前密码</label>
          <input
            v-model="formData.currentPassword"
            type="password"
            class="form-input"
            placeholder="请输入当前密码"
            @blur="validateField('currentPassword')"
            @input="clearError('currentPassword')"
          />
          <div v-if="errors.currentPassword" class="error-message">
            {{ errors.currentPassword }}
          </div>
        </div>
        
        <div class="form-group">
          <label class="form-label">新密码</label>
          <input
            v-model="formData.newPassword"
            type="password"
            class="form-input"
            placeholder="请输入新密码（8-32位）"
            @blur="validateField('newPassword')"
            @input="clearError('newPassword')"
          />
          <div v-if="errors.newPassword" class="error-message">
            {{ errors.newPassword }}
          </div>
        </div>
        
        <div class="form-group">
          <label class="form-label">确认新密码</label>
          <input
            v-model="formData.confirmPassword"
            type="password"
            class="form-input"
            placeholder="请再次输入新密码"
            @blur="validateField('confirmPassword')"
            @input="clearError('confirmPassword')"
          />
          <div v-if="errors.confirmPassword" class="error-message">
            {{ errors.confirmPassword }}
          </div>
        </div>
      </div>
      
      <div class="modal-footer">
        <button class="modal-btn secondary" @click="closeDialog">取消</button>
        <button 
          class="modal-btn primary" 
          :disabled="!isFormValid || loading"
          @click="handleSubmit"
        >
          <i v-if="loading" class="fas fa-spinner fa-spin"></i>
          {{ loading ? '修改中...' : '确认修改' }}
        </button>
      </div>
    </div>
  </div>
  </Teleport>
</template>

<script setup>
import { ref, reactive, computed, defineEmits } from 'vue';
import { ElMessage } from 'element-plus';
import { request } from '@/utils/auth.js';
import { validatePassword } from '../../../utils/validator';

// Emits
const emit = defineEmits(['close', 'success']);

// 响应式数据
const loading = ref(false);
const formData = reactive({
  currentPassword: '',
  newPassword: '',
  confirmPassword: ''
});

const errors = reactive({
  currentPassword: '',
  newPassword: '',
  confirmPassword: ''
});

// 验证单个字段
const validateField = (field) => {
  const value = formData[field];
  
  switch (field) {
    case 'currentPassword': {
      if (!value.trim()) {
        errors[field] = '请输入当前密码';
      } else {
        errors[field] = '';
      }
      break;
    }
      
    case 'newPassword': {
      const passwordResult = validatePassword(value);
      errors[field] = passwordResult.valid ? '' : passwordResult.message;
      
      // 如果确认密码已输入，重新验证确认密码
      if (formData.confirmPassword) {
        validateField('confirmPassword');
      }
      break;
    }
      
    case 'confirmPassword': {
      if (!value.trim()) {
        errors[field] = '请确认新密码';
      } else if (value !== formData.newPassword) {
        errors[field] = '两次输入的密码不一致';
      } else {
        errors[field] = '';
      }
      break;
    }
  }
};

// 清除错误
const clearError = (field) => {
  if (errors[field]) {
    errors[field] = '';
  }
};

// 表单是否有效
const isFormValid = computed(() => {
  const hasValues = formData.currentPassword && formData.newPassword && formData.confirmPassword;
  const noErrors = !Object.values(errors).some(error => error !== '');
  return hasValues && noErrors;
});

// 提交表单（使用统一的request函数，自动携带访问JWT）
const handleSubmit = async () => {
  Object.keys(formData).forEach(field => {
    validateField(field);
  });
  if (!isFormValid.value) {
    ElMessage.error('请检查输入内容');
    return;
  }
  loading.value = true;
  try {
    const result = await request('/api/auth/account-settings-api.php', {
      method: 'POST',
      headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
      body: new URLSearchParams({
        action: 'update_password',
        old_password: formData.currentPassword,
        new_password: formData.newPassword
      })
    });
    
    if (result.status === 'ok') {
      ElMessage.success(result.message || '密码修改成功');
      emit('success');
      closeDialog();
    } else if (result.message && result.message.includes('旧密码验证失败')) {
      ElMessage.error('旧密码错误，无法修改密码');
    } else {
      ElMessage.error(result.message || '密码修改失败');
    }
  } catch (error) {
    console.error('密码修改失败:', error);
    ElMessage.error('密码修改失败');
  } finally {
    loading.value = false;
  }
};

// 关闭对话框
const closeDialog = () => {
  emit('close');
};
</script>

<style scoped>
@import '../share/modal-styles.css';

.error-message {
  color: #ff6b6b;
  font-size: 12px;
  margin-top: 4px;
}
</style>
