<template>
  <Teleport to="body" v-if="visible">
    <div class="modal-overlay" @click="closeDialog">
      <div class="modal-content" @click.stop>
        <div class="modal-header">
          <h3 class="modal-title">{{ isEdit ? '重置用户密码' : '添加用户' }}</h3>
          <button class="modal-close" @click="closeDialog">
            <i class="fas fa-times"></i>
          </button>
        </div>
        
        <div class="modal-body">
          
          <!-- 编辑用户时显示当前信息 -->
          <div v-if="isEdit && user" class="current-info">
            <label class="info-label">目标用户</label>
            <div class="info-value">
              <div class="user-info-row">
                <span class="info-item">用户名: {{ user.username }}</span>
                <span class="info-item">ID: {{ user.id }}</span>
              </div>
            </div>
          </div>
          
          <!-- 用户名 -->
          <div class="form-group">
            <label class="form-label">用户名 *</label>
            <input
              v-model="formData.username"
              type="text"
              class="form-input"
              :placeholder="isEdit ? '保持为空则不修改' : '请输入用户名'"
              :disabled="isEdit"
              @blur="validateField('username')"
              @input="clearError('username')"
            />
            <div v-if="errors.username" class="error-message">
              {{ errors.username }}
            </div>
            <div class="form-hint">
              用户名用于登录，只能包含字母、数字和下划线，长度3-30位
            </div>
          </div>

          <!-- 密码 -->
          <div class="form-group">
            <label class="form-label">
              {{ isEdit ? '新密码 *' : '密码 *' }}
            </label>
            <div class="password-input-container">
              <input
                v-model="formData.password"
                :type="showPassword ? 'text' : 'password'"
                class="form-input"
                :placeholder="isEdit ? '请输入新密码' : '请输入密码'"
                @blur="validateField('password')"
                @input="clearError('password')"
              />
              <button
                type="button"
                class="password-toggle"
                @click="showPassword = !showPassword"
              >
                <i :class="showPassword ? 'fas fa-eye-slash' : 'fas fa-eye'"></i>
              </button>
            </div>
            <div v-if="errors.password" class="error-message">
              {{ errors.password }}
            </div>
            <div class="form-hint">
              {{ isEdit ? '输入新密码以重置用户密码' : '密码至少8位，包含大写、小写、数字、特殊字符' }}
            </div>
          </div>

          <!-- 邮箱 -->
          <div class="form-group">
            <label class="form-label">邮箱地址</label>
            <input
              v-model="formData.email"
              type="email"
              class="form-input"
              placeholder="请输入邮箱地址（可选）"
              @blur="validateField('email')"
              @input="clearError('email')"
            />
            <div v-if="errors.email" class="error-message">
              {{ errors.email }}
            </div>
            <div class="form-hint">
              邮箱用于找回密码和接收通知（可选）
            </div>
          </div>
        </div>
        
        <div class="modal-footer">
          <button class="modal-btn secondary" @click="closeDialog">取消</button>
          <button 
            :class="['modal-btn', isEdit ? 'danger' : 'primary']" 
            :disabled="!isFormValid || loading"
            @click="handleSubmit"
          >
            <i v-if="loading" class="fas fa-spinner fa-spin"></i>
            {{ loading ? (isEdit ? '重置中...' : '创建中...') : (isEdit ? '确认重置' : '创建用户') }}
          </button>
        </div>
      </div>
    </div>
  </Teleport>
</template>

<script setup>
import { ref, reactive, computed, watch, defineProps, defineEmits } from 'vue';
import { ElMessage } from 'element-plus';
import { apiRequest } from '@/utils/auth.js';
import { validateUsername, validatePassword, validateEmail } from '@/utils/validator.js';

// Props
const props = defineProps({
  visible: {
    type: Boolean,
    default: false
  },
  user: {
    type: Object,
    default: null
  },
  isEdit: {
    type: Boolean,
    default: false
  }
});

// Emits
const emit = defineEmits(['close', 'success']);

// 响应式数据
const loading = ref(false);
const showPassword = ref(false);

const formData = reactive({
  username: '',
  password: '',
  email: ''
});

const errors = reactive({
  username: '',
  password: '',
  email: ''
});

// 计算属性
const isFormValid = computed(() => {
  if (props.isEdit) {
    // 编辑模式：密码必填（因为我们使用reset_password接口）
    const passwordValidation = validatePassword(formData.password);
    return formData.password.trim() && 
           passwordValidation.valid &&
           !errors.username && 
           !errors.password && 
           !errors.email;
  } else {
    // 创建模式：用户名和密码必填
    const usernameValidation = validateUsername(formData.username);
    const passwordValidation = validatePassword(formData.password);
    return formData.username.trim() && 
           formData.password.trim() && 
           usernameValidation.valid &&
           passwordValidation.valid &&
           !errors.username && 
           !errors.password && 
           !errors.email;
  }
});

// 监听弹窗显示/隐藏
watch(() => props.visible, (visible) => {
  if (visible) {
    resetForm();
    if (props.isEdit && props.user) {
      // 编辑模式：填充现有数据
      formData.username = props.user.username || '';
      formData.email = props.user.email || '';
      formData.password = ''; // 密码留空
    }
  }
});

// 重置表单
const resetForm = () => {
  formData.username = '';
  formData.password = '';
  formData.email = '';
  
  errors.username = '';
  errors.password = '';
  errors.email = '';
  
  showPassword.value = false;
};

// 验证单个字段
const validateField = (field) => {
  const value = formData[field];
  
  switch (field) {
    case 'username': {
      if (!props.isEdit) {
        // 创建模式：用户名必填
        const validation = validateUsername(value);
        errors[field] = validation.valid ? '' : validation.message;
      } else {
        // 编辑模式：用户名不可修改
        errors[field] = '';
      }
      break;
    }
    case 'password': {
      // 创建模式和编辑模式都需要验证密码
      const validation = validatePassword(value);
      if (!props.isEdit) {
        // 创建模式：密码必填
        if (!value.trim()) {
          errors[field] = '请输入密码';
        } else {
          errors[field] = validation.valid ? '' : validation.message;
        }
      } else {
        // 编辑模式：密码必填
        if (!value.trim()) {
          errors[field] = '请输入新密码';
        } else {
          errors[field] = validation.valid ? '' : validation.message;
        }
      }
      break;
    }
    case 'email': {
      if (value.trim()) {
        // 如果有输入邮箱，则验证格式
        const validation = validateEmail(value);
        errors[field] = validation.valid ? '' : validation.message;
      } else {
        // 邮箱为可选项
        errors[field] = '';
      }
      break;
    }
  }
};

// 清除错误信息
const clearError = (field) => {
  errors[field] = '';
};

// 关闭弹窗
const closeDialog = () => {
  emit('close');
};

// 提交表单
const handleSubmit = async () => {
  // 验证所有字段
  validateField('username');
  validateField('password');
  validateField('email');
  
  if (!isFormValid.value) {
    ElMessage.error('请检查表单输入');
    return;
  }
  
  loading.value = true;
  
  try {
    if (props.isEdit) {
      // 编辑模式：暂时只支持重置密码
      if (!formData.password.trim()) {
        ElMessage.error('编辑模式下必须输入新密码');
        loading.value = false;
        return;
      }
      
      const bodyParams = new URLSearchParams();
      bodyParams.append('action', 'reset_password');
      bodyParams.append('user_id', props.user.id);
      bodyParams.append('new_password', formData.password.trim());
      
      console.log('发送参数:', Array.from(bodyParams.entries()));
      
      const result = await apiRequest('/api/auth/user-management-api.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: bodyParams
      });
      
      if (result.status === 'ok') {
        ElMessage.success('密码修改成功');
        emit('success');
      } else {
        // 先关闭弹窗，再显示错误信息
        emit('close');
        setTimeout(() => {
          ElMessage.error(result.message || '密码修改失败');
        }, 100);
      }
    } else {
      // 创建模式
      const bodyParams = new URLSearchParams();
      bodyParams.append('action', 'create_user');
      bodyParams.append('username', formData.username.trim());
      bodyParams.append('password', formData.password.trim());
      bodyParams.append('email', formData.email.trim());
      
      console.log('发送参数:', Array.from(bodyParams.entries()));
      
      const result = await apiRequest('/api/auth/user-management-api.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: bodyParams
      });
      
      if (result.status === 'ok') {
        ElMessage.success(result.message || '用户创建成功');
        emit('success');
      } else {
        // 先关闭弹窗，再显示错误信息
        emit('close');
        setTimeout(() => {
          ElMessage.error(result.message || '用户创建失败');
        }, 100);
      }
    }
  } catch (error) {
    console.error('操作失败:', error);
    // 先关闭弹窗，再显示错误信息
    emit('close');
    setTimeout(() => {
      ElMessage.error('网络错误，请稍后重试');
    }, 100);
  } finally {
    loading.value = false;
  }
};
</script>

<style scoped>
/* 引入现有的模态框样式 */
@import '../share/modal-styles.css';

/* 密码输入框特殊样式 */
.password-input-container {
  position: relative;
}

.form-input {
  padding-right: 45px;
}

.password-toggle {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  padding: 5px;
  border-radius: 4px;
  transition: color 0.2s ease;
}

.password-toggle:hover {
  color: rgba(255, 255, 255, 0.9);
}

/* 当前信息展示样式 */
.current-info {
  margin-bottom: 20px;
  padding: 15px;
  background: rgba(79, 172, 254, 0.1);
  border-radius: 8px;
  border-left: 4px solid #4facfe;
}

.info-label {
  display: block;
  font-weight: 600;
  color: #e8e8e8;
  margin-bottom: 8px;
  font-size: 14px;
}

.info-value {
  color: rgba(255, 255, 255, 0.8);
}

.user-info-row {
  display: flex;
  gap: 20px;
}

.info-item {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.9);
}

/* 错误信息样式 */
.error-message {
  color: #ff6b6b;
  font-size: 12px;
  margin-top: 5px;
  display: flex;
  align-items: center;
  gap: 5px;
}

.error-message::before {
  content: '⚠';
}

.form-hint {
  color: rgba(255, 255, 255, 0.6);
  font-size: 12px;
  margin-top: 5px;
  line-height: 1.4;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .user-info-row {
    flex-direction: column;
    gap: 5px;
  }
}
</style>
