<template>
  <div class="file-list-container">
    <div class="file-card">
      <div class="card-header">
        <h3>账号设置</h3>
      </div>
      <div v-if="!userProfile" class="loading-box">
        <i class="fas fa-circle-notch fa-spin"></i>
        <span>加载中...</span>
      </div>
      <div v-else class="profile-list">
        <!-- 用户名 -->
        <div class="profile-row">
          <span class="row-label">用户名：</span>
          <span class="row-value">{{ userProfile.username }}</span>
          <button class="action-btn small" @click="openUsernameDialog">
            <i class="fas fa-edit"></i> 修改
          </button>
        </div>
        <div class="profile-row">
          <span class="row-label">密码：</span>
          <span class="row-value">******</span>
          <button class="action-btn small" @click="openPasswordDialog">
            <i class="fas fa-key"></i> 修改
          </button>
        </div>
        <div class="profile-row">
          <span class="row-label">邮箱：</span>
          <span class="row-value">
            <span v-if="showEmail">{{ userProfile.email || '未设置' }}</span>
            <button v-if="!showEmail" class="action-btn small" @click="showEmail = true">
              <i class="fas fa-eye"></i> 显示邮箱
            </button>
          </span>
          <button v-if="showEmail" class="action-btn small" @click="openEmailDialog">
            <i class="fas fa-envelope"></i> 修改
          </button>
        </div>
        <div class="profile-row">
          <span class="row-label">二次验证：</span>
          <span class="row-value">
            <span :class="['mfa-status', userProfile.mfa_enabled ? 'enabled' : 'disabled']">
              {{ userProfile.mfa_enabled ? '已启用' : '未启用' }}
            </span>
          </span>
          <button class="action-btn small" @click="openMfaDialog">
            <i class="fas fa-shield-alt"></i> {{ userProfile?.mfa_enabled ? '管理' : '启用' }}
          </button>
        </div>
      </div>
      <!-- 弹窗区 -->
      <PasswordUpdateDialog
        v-if="showPasswordDialog"
        @close="showPasswordDialog = false"
        @success="handleUpdateSuccess"
      />
      <EmailUpdateDialog
        v-if="showEmailDialog"
        :current-email="userProfile?.email"
        @close="showEmailDialog = false"
        @success="handleUpdateSuccess"
      />
      <MfaSetupDialog
        v-if="showMfaDialog"
        :is-enabled="userProfile?.mfa_enabled"
        @close="showMfaDialog = false"
        @success="handleUpdateSuccess"
      />
      <UsernameUpdateDialog
        v-if="showUsernameDialog"
        :current-username="userProfile?.username"
        @close="showUsernameDialog = false"
        @success="handleUsernameUpdateSuccess"
      />
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import { ElMessage } from 'element-plus';
import { request } from '@/utils/auth.js';
import PasswordUpdateDialog from './PasswordUpdateDialog.vue';
import EmailUpdateDialog from './EmailUpdateDialog.vue';
import MfaSetupDialog from './MfaSetupDialog.vue';
import UsernameUpdateDialog from './UsernameUpdateDialog.vue';

const userProfile = ref(null);
const showPasswordDialog = ref(false);
const showEmailDialog = ref(false);
const showMfaDialog = ref(false);
const showUsernameDialog = ref(false);
const showEmail = ref(false);

const loadUserProfile = async () => {
  try {
    // 使用统一的request函数，自动携带访问JWT
    const result = await request('/api/auth/account-settings-api.php', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      body: 'action=get_info'
    });

    if (result.status === 'ok') {
      userProfile.value = {
        username: result.username,
        email: result.email,
        mfa_enabled: result.mfa_enabled
      };
      showEmail.value = false;
    } else if (result.status === 'visit_jwt_missing' || result.status === 'visit_jwt_invalid') {
      ElMessage.error(result.message || '访问令牌无效，请重新登录');
      // 触发重新登录事件
      window.dispatchEvent(new CustomEvent('auth:login-required', {
        detail: {
          reason: result.status,
          message: result.message
        }
      }));
    } else {
      ElMessage.error(result.message || '获取用户信息失败');
    }
  } catch (error) {
    console.error('获取用户信息失败:', error);
    ElMessage.error('获取用户信息失败');
  }
};

const openUsernameDialog = () => {
  showUsernameDialog.value = true;
};
const openPasswordDialog = () => {
  showPasswordDialog.value = true;
};
const openEmailDialog = () => {
  showEmailDialog.value = true;
};
const openMfaDialog = () => {
  showMfaDialog.value = true;
};
const handleUpdateSuccess = () => {
  loadUserProfile();
};
const handleUsernameUpdateSuccess = () => {
  loadUserProfile();
};

onMounted(() => {
  loadUserProfile();
});
</script>

